/* Sai Home page styles starts here */
section {
    overflow-x: hidden;
}

.sai-main-banner {
    background: url(../img/herosec-bg.png) no-repeat left bottom;
    background-size: cover;
    display: block;
}

.sai-herosec-right {
    background: url(../img/sai-banner-img1.png) no-repeat right top;
    background-size: cover;
    min-height: 790px !important;
}

ul.left-menu {
    list-style: none;
    margin-top: 80px;
}

ul.left-menu li {
    margin: 10px 0;
}

ul.left-menu li a {
    padding: 8px 0;
    display: block;
    color: #B5263D;
}

.sai-top-nav ul {
    list-style: none;
    float: right;
    margin-right: 30px;
    margin-top: 30px;
}
.sai-top-nav ul li {
    float: left;
    margin-left: 50px;
}

.sai-top-nav ul li a {
    color: #FFFFFF;
}
.hero-sec-content {
    position: absolute;
    bottom: 35%;
    left: 30px;

}
.hero-sec-content h1 {
    color: #FFFFFF;
    font-size: 48px;
}
.hero-sec-content p {
    color: #FFFFFF;
    font-size: 20px;
}
.thoughtforDay h3 {
    color: #5F2E2A;
    font-size: 28px;

}
.thoughtforDay a {
    color: #B5263D;
    text-decoration: underline;
}
.thoughtforDay ul {
    list-style: none;
    padding: 0;
}
.thoughtforDay ul li {
    float: left;
    margin-right: 30px;
}
.services-sec {
    background: url(../img/services-bg.png) no-repeat top left;
    background-size: cover;
}

.bg-orange {
    background: #FFA68D !important;
}
.active-projects h5, .news-sec h5 {
    color: #646464;

}

.active-projects .slider-highlight-selected [aria-selected="false"] {
    opacity: 1;
    transform: scale(1);
    pointer-events: none;
}

.news-sec .slider-highlight-selected [aria-selected="false"] {
    opacity: 1;
    transform: scale(1);
    pointer-events: none;
}
.active-projects .flickity-enabled.is-draggable, .news-sec .flickity-enabled.is-draggable {
    overflow: hidden;

}

.bg-white-orange {
    background: #FFF5F2;
}

.event-sec .flickity-prev-next-button.previous, .event-sec .flickity-prev-next-button.next {
    display: none !important;
}

